.nav {

  &__list {
    @include list-reset;
    display: flex;
    align-items: baseline;
  }

  &__item:not(:last-child) {
    position: relative;
    margin-right: 15px;
    padding-right: 20px;

    &:not(:nth-last-child(2))::after {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      width: 4px;
      height: 4px;
      margin-top: -2px;
      border-radius: 50%;
      background-color: #ffffff;
    }

    &:nth-last-child(2) {
      padding-right: 0;
    }

    &--button {
      margin-right: 0;
    }
  }

  &__link {
    @include link;

    &:hover {
      text-decoration: underline;
    }

    &:active {
      color: rgba($color: #FFFFFF, $alpha: 0.5);
    }
  }
}